 // eslint-disable-next-line no-undef
 importScripts('//cdnjs.cloudflare.com/ajax/libs/xlsx/0.18.5/xlsx.core.min.js');

// eslint-disable-next-line no-undef
//importScripts('//cdn.jsdelivr.net/npm/json-aggregate@0.1.2/index.min.js');

// eslint-disable-next-line no-undef
//const result = `From worker: worker started at ${moment().format('HH:mm:ss')}`


  // eslint-disable-next-line no-restricted-globals
  self.addEventListener("message", (e) => {
  
  
  const sum = e.data.map(item => Number(item.Amount)).reduce((a,b)=> a+b ).toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  let gl_amt = e.data.reduce((result, item) => {
    const key = item.GL_CODE;
    if (!result[key]) {
      result[key] = { GL_CODE: key, Amount : 0 };
    }
    result[key].Amount += Number(item.Amount);
    return result;
  }, {});
  gl_amt = Object.values(gl_amt);
  gl_amt = gl_amt.map(item => ({...item, Amount: item.Amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}));
  

  let mth_amt = e.data.reduce((result, item) => {
    const key = item.POSTING_DATE;
    if (!result[key]) {
      result[key] = { POSTING_DATE : key, Amount : 0 };
    }
    result[key].Amount += Number(item.Amount);
    return result;
  }, {});
  mth_amt = Object.values(mth_amt);
  mth_amt = mth_amt.map(item => ({...item, Amount: item.Amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}));

  let cc_amt = e.data.reduce((result, item) => {
    const key = item.COST_CENTER;
    if (!result[key]) {
      result[key] = { COST_CENTER : key, Amount : 0 };
    }
    result[key].Amount += Number(item.Amount);
    return result;
  }, {});
  cc_amt = Object.values(cc_amt);
  cc_amt = cc_amt.map(item => ({...item, Amount: item.Amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}));
  
  
  // eslint-disable-next-line no-restricted-globals
  self.postMessage({
      total: sum,
      gl: gl_amt,
      mth: mth_amt,
      cc: cc_amt
    })
  });